@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
li  {
    list-style: none;
}

.desktop-nav-links li a{
    transition: color .3s ease;
}
.desktop-nav-links li a:hover {
    color: hsl(255, 11%, 22%);
}
.desktop-nav-links {
    /* padding: 0; */
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    grid-column: 2/5;
    
}

.desktop-login-buttons {
    grid-gap: 2rem;
    gap: 2rem;
    display: flex;
    align-items: center;
    grid-column: span 1/30;
}


.mobile-nav {
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: hsl(257, 27%, 26%);
  width: calc(100% - 64px);
  z-index: 1000;
  border-radius: 10px;
  overflow: hidden;
  /* transition: max-height 0.5s ease; */
}

.mobile-login-buttons,
.mobile-nav-links {
  display: flex;
  flex-direction: column;
}

.mobile-nav-links {
  margin: 0 auto;
  padding: 2rem 0;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.mobile-nav-links a {
  color: white;
}

.mobile-login-buttons {
  grid-gap: 1.5rem;
  gap: 1.5rem;
  border-top: solid 1px hsl(0, 0%, 75%, 0.25);
  padding: 2rem 1.5rem;
}

@media (min-width: 567px) {
  .mobile-nav {
    width: 89vw;
  }
}

.hamburger {
  z-index: 100;
  cursor: pointer;
}

.hamburger svg {
  color: hsl(257, 7%, 63%);
}

a {
    outline: none;
    text-decoration: none;
    color: hsl(257, 7%, 63%);
    font-weight: 700;

}

.logo img {
    max-width: 100%;
}

nav {
    font-size: 15px;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: subgrid;
    justify-content: space-between;
    align-items: center;
}



.hero {
    margin-top: 2rem;
    margin-bottom: 2rem;
    /* padding: 5rem 0; */
    display: flex;
    align-items: center;
    position: relative;
    flex-direction: column;
}

.hero >* {
    flex-basis: 100%;
}

.hero-text {
    padding-top: 2rem;
    order: 2;
    text-align: center
}

.hero-text h1 {
    color: hsl(255, 11%, 22%);
    font-size: 2.5625rem;
}

.hero p {
    color: hsl(257, 7%, 63%);
    padding: 1rem 0;
    font-size: 1.125rem;
}

.hero-img {
    padding: 3rem 0;
}

.hero-img img{
    max-width: 100%;
    height: auto;
    transform: translateX(30%) scale(1.25);
}

@media screen and (max-width: 465px) {
    .hero-img img {
        transform: translateX(30%) scale(1.5);
    }
}

@media screen and (min-width: 667px) {
    main {
        margin: 0;
        margin-top: min(20%, 6rem);
    }

    .hero {
        display: grid;
        grid-auto-flow: column;
        place-items: center;
        margin: 1.625rem auto;
        margin-right: 0rem;
        /* justify-content: space-between; */
        grid-gap: 5rem;
        gap: 5rem;
        width: 100%;
    }
    
    .hero-text { 
        padding: 2rem 0;
        text-align: left;
        order: 1;
    }

    .hero-text h1 {
        font-size: 4.75rem;
    }

    .hero-text p {
        font-size: 1.375rem;
        padding: 1rem 0;
    }

    .hero-img{
        order: 2;
    }
    
    .hero-img img {
        transform: translateX(50%) scale(2.25);
    }


}
.link {
    display: flex;
    align-items: center;
    margin: 1.5rem 0;
    padding: 1rem;
    background-color: white;
    grid-gap: 1rem;
    gap: 1rem;
    border-radius: 10px;
    width: 100%;
}

.link> *:nth-child(1) {
    flex: 1 1 65%;
    min-width: 15ch;
}

.link> *:nth-child(2) {
    text-align: right;
    flex: 1 1 25%;
    min-width: 25ch;
    color: hsl(180, 66%, 49%);
}

.link> *:nth-child(3) {
    flex: 1 1 10%;
    min-width: 10ch;
}

.link-text p,
.link-text-result p {
    word-wrap: anywhere;
    max-width: 100%;
}


@media (max-width: 667px) {
    .link {
        flex-direction: column;
        align-items: flex-start;
    }

    .link-text p,
    .link-text-result p {
        font-size: 1rem;
    }

    .link-text-result p{
        text-align: left;
    }

    .link > *:nth-child(2) {
        padding-top: 1rem;
        border-top: solid 1px hsl(0, 0%, 75%);
        width: 100%;
    }

    .link .MuiButton-root {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
}
input {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    border-radius: 7.5px;
    padding: 0.75rem;
}

.redBorder {
    padding: 0 0 0 0.75rem;
    border: 5px solid hsl(0, 87%, 67%);
}

.link-field-container {
    /* position: absolute;
    top: 50%; */
    margin-top: -7rem;
    display: flex;
    overflow: hidden;
    grid-gap: 1rem;
    gap: 1rem;
    background-color: hsl(257, 27%, 26%);
    background-image: url(/shortly-app/static/media/bg-shorten-desktop.0d9742f4.svg);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 2.5rem 3rem;
    border-radius: 10px;
}

.link-text-field {
    /* background-color: white; */
    border-radius: 10px;
}

.link-field-container > *:nth-child(1) {
    flex: 1 1 75%;
    min-width: 25ch;
}

.link-field-container > *:nth-child(2) {
    flex: 1 1 25%;
    min-width: 15ch;
}

.link-container .MuiTextField-root {
    font-family: 'Poppins', sans-serif;
}

.error-warning {
    color: hsl(0, 87%, 67%);
    font-size: 1rem;
    font-weight: 500;
}

@media (max-width: 667px) {
    .link-field-container {
        padding: 1.5rem;
        background-image: url(/shortly-app/static/media/bg-shorten-mobile.41c34b33.svg);
        flex-direction: column;
    }
    
    .link-field-container > *:nth-child(1) {
        min-width: 15ch;
        max-width: 100%;
    }

    .error-warning {
        font-size: 10px;
    }
}
.articles-desc {
    margin: 0 auto;
    padding: 8.25rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 6.75rem;
    gap: 6.75rem;
}

h2, h3 {
    color: hsl(255, 11%, 22%);
}

.articles-desc p {
    color: hsl(257, 7%, 63%);
}

.articles-desc-title {
    text-align: center;
    max-width: 33.875rem;
}

.articles-desc-title h2 {
    font-size: 2.25rem;
}

.articles-desc-title p {
    font-size: 1.125rem;
}

.articles-boxes {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1.875rem;
    gap: 1.875rem;
}

.articles-boxes > * {
    flex: 1 1 18em;
    margin-bottom: 2rem;
    z-index: 100;
}


.article-box {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 10px;
    padding: 1rem 2rem;
    padding-top: 0;
}

.article-img {
    transform: translateY(-45%);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: hsl(257, 27%, 26%);
    padding: 1rem;
    border: none;
    border-radius: 50%;
}

@media (min-width: 1245px) {
    .articles-boxes::after {
        content: "";
        background-color: hsl(180, 66%, 49%);
        transform: translate(15rem, -10rem);
        height: 50%;
        width: .5rem;
        z-index: 0;
    }

    .articles-boxes >:nth-child(2) {
        transform: translateY(2.5rem);
    }
    .articles-boxes >:nth-child(3) {
        transform: translateY(5rem);
    }
}

@media (max-width: 667px) {
    /* .articles-boxes::after {
        content: "";
        background-color: hsl(180, 66%, 49%);
        transform: rotate(90deg) translate(-32rem, -.05rem);
        width: 100%;
        height: .5rem;
        z-index: 0;
    } */
    .articles-desc-title h2 {
        font-size: 1.75rem;
    }

    .articles-desc-title p {
        font-size: 1rem;
    }

    .articles-boxes {
        grid-gap: 2rem;
        gap: 2rem;
    }

    .article-box {
        align-items: center;
        text-align: center;
    }

    .article-box h3 {
        font-size: 1.375rem;
    }

    .article-box p {
        font-size: 0.9375rem;
    }
}
.boost {
    text-align: center;
    color: white;
}

.boost h1 {
    padding-bottom: 1.5rem;
}

@media (max-width: 667px) {
    .boost h1 {
        font-size: 1.6875rem;
    }
}
footer h4 {
    text-transform: capitalize;
    color: white;
    font-size: 1rem;
    padding-bottom: .75rem;
}

footer p {
    cursor: pointer;
    padding: .25rem 0;
    text-transform: capitalize;
    color: hsl(0, 0%, 75%);
    font-size: 0.875rem;
    transition: color .3s ease;
}

.footer,
.footer-right,
.footer-information,
.social-media {
    display: flex;
}

.footer {
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}

.footer-right {
    justify-content: space-between;
    grid-gap: 3rem;
    gap: 3rem;
}

.footer-information {
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}

.social-media {
    align-items: baseline;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

footer svg {
    color: white;
    cursor: pointer;
    transition: color .3s ease;
}

footer svg:hover,
footer p:hover {
    color: hsl(180, 66%, 49%);
}

@media (max-width: 667px) {
    .footer,
    .footer-right,
    .footer-information {
        flex-direction: column;
        text-align: center;
    }

    .footer {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .footer > svg {
        margin: 0 auto;
    }

    .social-media {
        flex-direction: row;
        justify-content: center;
    }

    .footer-right {
        grid-gap: 1rem;
        gap: 1rem;
}

    .footer-information {
        grid-gap: 2rem;
        gap: 2rem;
    }
}
